.glow{
    font-size: 9vw;
    line-height: 9vw;
    text-shadow: 0 0 3vw #F40A35;
}
.glow{
    animation: glow 1.2s ease infinite;
    -moz-animation: glow 1.2s ease infinite;
    -webkit-animation: glow 1.2s ease infinite;
}
@keyframes glow {
    0%,
    100% {
      text-shadow: 0 0 1vw #FFFF, 0 0 3vw #FFFF, 0 0 10vw #FFFF, 0 0 10vw #FFFF, 0 0 .4vw #FED128, .5vw .5vw .1vw #806914;
      color: black;
    }
    50% {
      text-shadow: 0 0 .5vw #FFFF, 0 0 1.5vw #FFFF, 0 0 5vw #FFFF, 0 0 5vw #FFFF, 0 0 .2vw #800E0B, .5vw .5vw .1vw #40340A;
      color: black;
    }
  }